.App-header {
  background-color: #adb5bd;
  color: white;
  padding: 32px;
}

.App-content{
  margin: 32px;
}

.title {
  color: #757575;
  font-weight: 550;
  font-size: 24px;
  margin-bottom: 20px;
}
.container {
  margin: 0;
  width: 100%;
  max-width: 100%;
}

.row {
  background-color: white;
  border-bottom: 1.5px solid #f1f1f1;
  height: 72px;
  font-size: 16px;
  line-height: 24px;
  color: #505050;
  font-weight: 400;
}

.row-header {
  padding: 16px 0;
  font-size: 14px;
  color: #757575;
  font-weight: 500;
  line-height: 16px;
  height: 48px;
}

.row i {
  font-size: 24px;
  color: #909090;
}
.col-3 {
  padding-left: 0;
}
.col-4 {
  padding-left: 24px;
}

.col-name {
  padding-left: 24px;
}

#sorting:hover {
  color: #505050;
  font-weight: 600;
}

.edit-form {
  max-width: 100%;
  width: 100%;
}

.form-row {
  background-color: white;
  margin: 0 0 10px 0;
  padding-left: 7px;
}
.form-edit {
  margin-top: -1px;
}

.form-group {
  margin-top: 16px;
}

.form-group input {
  border-radius: 0;
  background-color: #f7f7f7;
  height: 40px;
  font-size: 16px;
  line-height: 24px;
  color: #505050;
  font-weight: 400;
}

#btn-sub {
  color: #808080;
  width: 100px;
  font-weight: 600;
  margin-left: 10px;
  margin-right: 10px;
}

.btn-cancel2 {
  color: #007bff;
  margin-right: 10px;
}

.btn-1 {
  margin-right: 10px;
}

.btn {
    width: 88px;
    font-weight: 600;
    font-size: 16px;
    height: 40px;
    border-radius: 0;
}




